/* Global Styles */

* {
    margin: 0;
}

.btn {
    font-family: 'Satoshi', sans-serif;
    margin-top: 10px;
    border-radius: 0px !important;
    border-color: #E31382 !important;
    color: #E31382 !important;
}

.btn:hover {
    background-color: #E31382 !important;
    border-color: white !important;
    color: white !important;
}


/* Hero Section */

.hero {
    margin: 25% 15%;
}

.hero h1 {
    font-family: 'Satoshi', sans-serif;
    font-size: 80px;
    font-weight: 700;
}

.hero span {
    color: #E31382;
}

/* About Section */

.about {
    /* margin: 30% 20% 20%; */
    padding: 5% 20% 20%;
    position: relative;
    background-image: linear-gradient(135deg,#E31382 0%,#df3b92 100%);
    clip-path: polygon(0 0,100% 0,100% 80%,0 100%);
    color: white;
}

.about h1 {
    font-family: 'Satoshi', sans-serif;
    font-size: 60px;
    font-weight: 700;
    text-align: center;
    margin-bottom: 10%;
    /* color: white; */
}

.about img {
    width: 320px;
    height: auto;
}

.about-description {
    font-family: 'Satoshi', sans-serif;
    font-size: 18px;
    float: right;
    width: 55%;
    align-content: center;
    margin-top: 5%;
    /* color: white; */
}

.about .btn {
    color: white !important;
    border-color: white !important;
}

.about .btn:hover {
    background-color: white !important;
    color: #E31382 !important;
}

/* Project Section */

/* .project {
    max-width: 1024px;
    margin: 0 auto;
    width: 100%;
} */

.project h1 {
    font-family: 'Satoshi', sans-serif;
    font-size: 60px;
    font-weight: 700;
    text-align: center;
}

.project-section {
    /* width: 100%; */
    display: grid; 
    grid-template-columns: 1fr 1fr;
    gap: 5rem;
    padding: 10%;
}

.project-section h2 {
    font-family: 'Satoshi', sans-serif;
    font-weight: 500;
    color: #E31382;
}

.project-section p {
    font-family: 'Satoshi', sans-serif;
    font-size: 18px;
    float: left;
    width: 100%;
    align-content: center;
    /* border: solid 5px red; */
}

.project-section img {
    width: 600px;
    height: auto;
    float: right;
}

.project-section a {
    text-decoration: none;
}

/* Contact Section */

.contact {
    /* margin: 10% 20% 5%; */
    /* border: solid 5px red; */
    color: white;
    padding: 5% 20% 20%;
    height: 600px;
    background-image: linear-gradient(135deg,#E31382 0%,#df3b92 100%);
    clip-path: polygon(0 10vh,100% 0,100% 100%,0 100%);
}

.contact h1 {
    font-family: 'Satoshi', sans-serif;
    font-size: 60px;
    font-weight: 700;
    text-align: center;
    margin-bottom: 10%;
    /* color: white; */
    /* border: solid 5px red; */
}

.contact p {
    font-family: 'Satoshi', sans-serif;
    font-size: 20px;
    float: left;
    width: 100%;
    align-content: center;
    /* color: white; */
    /* border: solid 5px red; */
}

.contact a {
    text-decoration: none;
    font-family: 'Satoshi', sans-serif;
    font-size: 20px;
    color: white;
    margin-bottom: 15px;
}

.contact .personal-info {
    display: grid; 
    float: left;
    /* border: solid 5px red; */
}

.contact .icon-link {
    align-items: baseline;
    gap: 1rem;
}

.contact form {
    float: right;
    /* border: solid 5px red; */
    width: 60%;
}

.contact input,
.contact textarea {
    font-family: 'Satoshi', sans-serif;
    border: solid 1px gray;
    border-radius: 8px;
}

.contact .btn {
    color: white !important;
    border-color: white !important;
}

.contact .btn:hover {
    background-color: white !important;
    color: #E31382 !important;
}

/* Footer Section */

footer {
    font-family: 'Satoshi', sans-serif;
    font-size: 20px;
    height: 350px;
    background-color: #333333;
    padding: 4.8rem 0; 
    text-align: center;
    color: white;
}

footer p {
    padding-top: 50px;
}

.footer-links {
    display: flex;
    justify-content: center;
}


.icons {
    padding-top: 50px;
    transition: 0.3s;
}

.icons:hover {
    transform: translate(0, -10px);
    cursor: pointer;
}

/* Media Queries */


@media screen and (max-width: 1426px) {
    .hero h1 {
        font-size: 65px;
    }
}

@media screen and (max-width: 1370px) {
    .hero h1 {
        font-size: 60px;
    }
}

@media screen and (max-width: 1299px) {
    .contact form {
        width: 50%;
    }
}

@media screen and (max-width: 1220px) {
    .hero {
        margin: 30% 15%;
    }

    .hero h1 {
        font-size: 55px;
    }

    .about {
        padding: 5% 15% 15%;
    }
}

@media screen and (max-width: 1185px) {
    .about {
        padding: 5% 15% 15%;
    }
}

@media screen and (max-width: 1114px) {
    .hero {
        margin: 35% 15%;
    }

    .hero h1 {
        font-size: 50px;
    }

    .project-section img {
        width: 500px;
    }

    .contact {
        padding: 10% 20% 20%;
    }
}

@media screen and (max-width: 1041px) {
    .hero {
        margin: 35% 10%;
    }
    .about {
        padding: 5% 10% 15%;
    }

    .contact {
        padding: 10%;
    }
}

@media screen and (max-width: 1008px) {
    .project-section img {
        width: 450px;
    }
}